function Makelist(props){
    return(
    props.day.map(list => (
        <li className="list_li" onClick={(e)=>e.target.classList.toggle("active")} >
            <ul className="list_ul" >
                <li key={list.num} className="list_num">{list.num}<div className="list_name">{list.name}</div></li>
                <li key={list.time} className="list_time">일정 : {list.time}</li>
                <li key={list.pointer} className="list_pointer">{list.pointer}</li>
                <li key={list.info} className="list_info">정보 : {list.info}</li>
                <li key={list.call} classNmae="list_call">전화 :&nbsp;<a href="tel:+{list.call}+">{ list.call}</a>&nbsp;&nbsp;<a href={list.homepage}>{list.homepage}</a></li>
            </ul>
        </li>
    ))
    )
} 

function todolist(){
    return(
        <ul className="list">
            <div className="daylist first">첫째날</div>
            <Makelist day={firstdaylist}></Makelist>
            <div className="daylist second" id="2">둘째날</div>
            <Makelist day={seconddaylist}></Makelist>
        </ul>
    )
}
