import { useState } from 'react';
import './App.css';
import jejumap from './jejumap.jpg';
import Todolist from './dolist';

function Start(){
  return(
    <div className="gotrip" onClick={ e => {
      e.target.classList.add("active");
      document.querySelector(".navibutton").classList.add("on");
      document.querySelector(".container").classList.add("active");
        }
      }
    >여행시작</div>
  )
}

function openNav(){
  document.querySelector(".container.active").style.marginLeft = "160px";
  document.querySelector(".leftnavi").style.width = "160px";
  document.querySelector(".container.active").style.opacity="0.1" ;
}

function closeNav() {
  document.querySelector(".leftnavi").style.width = "0";
  document.querySelector(".container.active").style.marginLeft = "0";
  document.querySelector(".container.active").style.opacity = "1";
}


function Container(props){
  return(
  <div className="container">
    
    <TopNavigation mode={props.mode}></TopNavigation>
    {props.children}
  </div>
  )
}

function LeftNavigation(props){
  let [navimode, setmode] = useState("closed")
  return(
    <nav className="leftnavi">
      <div className="navibutton" onClick={(e)=>{
      if(navimode==="closed"){
        openNav();
        e.target.style.backgroundImage = "url('https://image.flaticon.com/icons/png/512/561/561189.png')"
        setmode("open")
      } else if(navimode==="open"){
        closeNav();
        e.target.style.backgroundImage = "url('https://image.flaticon.com/icons/png/512/660/660376.png')"
        setmode("closed")
      }}}></div>
    <a href="#" onClick={()=> {
      window.scrollTo(0, 0);
      closeNav();
      document.querySelector(".navibutton").style.backgroundImage = "url('https://image.flaticon.com/icons/png/512/660/660376.png')"
      setmode("closed");
    }}>첫째날</a>
    <a href="#2" onClick={()=> {
      closeNav();
      document.querySelector(".navibutton").style.backgroundImage = "url('https://image.flaticon.com/icons/png/512/660/660376.png')"
      setmode("closed");
    }}>둘째날</a>
    </nav>
  )
} 

function TopNavigation(props){
  return(
    <>
    <img className="jejumap" id="1" src={jejumap} alt="제주도 지도"></img>
    </>
  )
} 




function App() {
  const [mode, setmode] = useState("첫째날")
  const changeday = (mode) => setmode(mode)
  return (
    <div className="App">
      <Start></Start>
      <LeftNavigation navi={["첫째날","둘째날","셋째날"]}></LeftNavigation>
      <div className="topnavi">{mode}</div>
      <Container mode={mode}>
      <Todolist></Todolist>
      </Container> 
    </div>
  );
}

export default App;
